@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700");
*,
*:before,
*:after,
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: none;
  box-sizing: border-box;
  vertical-align: baseline;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: contain;
  -webkit-font-smoothing: antialiased;
}

*:before,
*:after {
  font-size: inherit;
  font-family: inherit;
  font-weight: inherit;
  text-decoration: inherit;
}

::selection {
  background-color: #008da3;
  text-shadow: none;
  color: white;
}

input::placeholder,
input::-webkit-input-placeholder,
input::-moz-placeholder {
  opacity: 0.85;
}

a {
  color: #0942a4;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

address {
  font-style: normal;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

figcaption {
  display: none;
}

body,
button,
input,
select,
textarea {
  font-family: "Open Sans", sans-serif;
}

button,
input,
select,
textarea {
  appearance: none;
}

button,
input,
p,
select,
textarea {
  font-size: 1rem;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}

body {
  background-color: #ffffff;
}
body.no-scroll {
  overflow: hidden;
}

button {
  background-color: transparent;
  cursor: pointer;
}

.clear {
  clear: both;
}

*:disabled {
  cursor: not-allowed;
}

.d-none {
  display: none !important;
}

.v-hidden {
  position: absolute;
  left: -99999px;
}

fieldset {
  min-width: initial;
  display: block;
}

form:not(.width-auto) {
  width: 100%;
}

h1:not(:last-child),
h2:not(:last-child),
h3:not(:last-child),
h4:not(:last-child),
h5:not(:last-child),
h6:not(:last-child),
p:not(:last-child) {
  padding-bottom: 0.625rem;
}

p:empty {
  display: none;
}

html,
body {
  min-height: 100vh;
  -webkit-overflow-scrolling: touch;
  color: #282828;
}

html {
  font-size: 1em;
}

iframe {
  width: 100%;
  height: 100%;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
}

.link {
  cursor: pointer;
  text-decoration: none;
  color: #0b56d5;
  font-weight: 700;
}
.link:hover {
  text-decoration: underline;
}
.link:disabled {
  cursor: not-allowed;
}

ol:not(.has-bullet-points),
ul:not(.has-bullet-points) {
  list-style: none;
}
ol.has-bullet-points,
ul.has-bullet-points {
  padding-left: 20px;
}
ol.has-bullet-points.list-style-disc,
ul.has-bullet-points.list-style-disc {
  list-style: disc;
}
ol:not(:last-child),
ul:not(:last-child) {
  padding-bottom: 0.625rem;
}

p {
  word-break: break-word;
}
p:empty {
  padding-bottom: 0rem;
}

section {
  width: 100%;
  position: relative;
}

select::-ms-expand {
  display: none;
}

.svg-inline-items {
  display: none;
  opacity: 0;
  visibility: visible;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}
table caption {
  display: none;
  visibility: hidden;
  opacity: 0;
}

.text-off-screen {
  text-indent: -99999px;
}

.bold {
  font-weight: bold;
}

.error-text {
  color: #E22C2C;
}

.word-break-initial,
.word-break-initial * {
  word-break: initial !important;
}

body {
  background-color: #f1f3f6;
  position: relative;
}

header {
  background-color: #fff;
  padding: 15px 10px;
  box-shadow: 0 3px 3px -3px rgba(0, 0, 0, 0.3);
  display: grid;
  grid-template-columns: 50px auto 50px;
  grid-gap: 10px;
}
header svg {
  display: block;
  width: 100%;
  height: 100%;
}
header a,
header button {
  height: 30px;
  justify-self: center;
  align-self: center;
  height: 30px;
}
header button {
  width: 30px;
  padding: 5px;
}

.search-button {
  background-color: #262637;
  padding: 10px;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}
.search-button button {
  width: 100%;
  background-color: #fff;
  padding: 10px;
  font-size: 16px;
  font-weight: 600;
}

.search-assets-container {
  position: relative;
  margin: 0 auto;
  max-width: 500px;
}
.search-assets-container input {
  padding: 15px 15px 15px 50px;
  border: 1px solid #206EF3;
  width: 100%;
  border-radius: 50px;
}
.search-assets-container button {
  width: 30px;
  height: 30px;
  padding: 5px;
  position: absolute;
  top: 10px;
  left: 10px;
}
.search-assets-container button svg {
  width: 100%;
  height: 100%;
}